export const getCommodityClassificationList = (data) => {
	return uni.$http.post(
			'/commodity/getClassificationByPage',
			data
	)
}

/**
 * id
 */
export const getCommodityListByClassification = (data) => {
	return uni.$http.get(
			'/commodity/getCommodityByClassificationId/' + data
	)
}

